<template>
    <div class="NAME-CARD ROOT">
        
        <text-view class="NAME-CARD-ITEM" id="NAME-CARD-TITLE" :data="{text:data.title}"  v-if="!!data.title" />
        <text-view class="NAME-CARD-ITEM" id="NAME-CARD-SUBTITLE" :data="{text:data.subtitle}" v-if="!!data.subtitle" />
        <text-view class="NAME-CARD-ITEM" id="NAME-CARD-TEXT" :data="{text:data.text}"  v-if="!!data.text" />
        <text-view class="NAME-CARD-ITEM" id="NAME-CARD-SUBTEXT" :data="{text:data.subtext}"  v-if="!!data.subtext" />

    </div>
</template>
<script>
import v from "../validators"
var {$OR, vString,vObject,vPromise,vURL}=v;
import textView from "./v.s.text.vue"
export default
{
    name:"NAME-CARD",
    components:{
        textView
    },
    props:['data']
}
</script>
<style lang="less">
.NAME-CARD
{
    
    display:inline-grid;
    grid-template-columns:1fr;
    grid-auto-rows:max-content;
    width:100%;
    height:100%;
    background:black;
    color:white;
    box-sizing:border-box;
    // mix-blend-mode:multiply;
    // border:dashed 1px;
    // backdrop-filter:blur(1px);
    #NAME-CARD-TITLE
    {
        font-size:3em;
    }
    #NAME-CARD-SUBTITLE
    {
        font-size:1.6em;
    }
    #NAME-CARD-TEXT
    {
        font-size:1em;
    }
    #NAME-CARD-SUBTEXT
    {
        font-size:0.6em;
    }
}
</style>


